import URLSearchParams from './URLSearchParams';

// 'window' isn't defined in mocha test
const currentWindowUrl: typeof window.URL | undefined = (typeof window !== 'undefined') ? window.URL : undefined;

/**
 * This class contains functionality that is common between the smaller "URL" class and the experimental
 *  complete "URL" class.
 */
abstract class BaseURL implements URL {
  private _searchParams: URLSearchParams;

  public static createObjectURL(object: any, options?: ObjectURLOptions): string { // tslint:disable-line:no-any
    if (currentWindowUrl && currentWindowUrl.createObjectURL) {
      // Passthrough to the existing currentWindowUrl if the browser supports it
      return currentWindowUrl.createObjectURL(object, options);
    } else {
      throw new Error('createObjectURL not supported');
    }
  }

  public static revokeObjectURL(objectUrl: string): void {
    if (currentWindowUrl && currentWindowUrl.revokeObjectURL) {
      // Passthrough to the existing revokeObjectURL if the browser supports it
      currentWindowUrl.revokeObjectURL(objectUrl);
    } else {
      throw new Error('revokeObjectURL not supported');
    }
  }

  constructor(url: string, base?: string) { /* empty */ }

  public abstract get href(): string;
  public abstract set href(value: string);

  public abstract get origin(): string;

  public abstract get protocol(): string;
  public abstract set protocol(value: string);

  public abstract get username(): string;
  public abstract set username(value: string);

  public abstract get password(): string;
  public abstract set password(value: string);

  public abstract get host(): string;
  public abstract set host(value: string);

  public abstract get hostname(): string;
  public abstract set hostname(value: string);

  public abstract get port(): string;
  public abstract set port(value: string)

  public abstract get pathname(): string;
  public abstract set pathname(value: string);

  public get search(): string {
    return this._query || '';
  }
  public set search(newValue: string) {
    this._invalidate();

    // Trim the leading "?" and serialize "null" or "undefined" as a weird caveat of the spec
    const queryMatches: string[] | null = `${newValue}`.match(/^\??(.*)/);
    if (queryMatches && !!queryMatches[1]) {
      this._query = queryMatches[1];
    } else {
      this._query = null; // tslint:disable-line:no-null-keyword
    }

    const existingSearchParams: string = this._searchParams.toString();
    if (existingSearchParams !== this.search && `?${existingSearchParams}` !== this.search) {
      // Update searchParams
      (this._searchParams as any)._initialize(this.search); // tslint:disable-line:no-any
    }
  }

  public get searchParams(): URLSearchParams {
    return this._searchParams;
  }

  // Need to include this because the TypeScript lib.d.ts typings have the incorrect case
  // Remove when the TS typings are fixed. VSO#339963
  public get searchparams(): URLSearchParams {
    throw new Error('This function does not exist.');
  }

  public abstract get hash(): string;
  public abstract set hash(value: string);

  public toJson(): string {
    // Same behavior as "href"
    return this.href;
  }

  protected abstract _invalidate(): void;

  protected _initializeSearchParams(): void {
    this._searchParams = new URLSearchParams(this.search);
    // Ensure searchParams has a pointer to this object.
    (this._searchParams as any)._url = this; // tslint:disable-line:no-any
  }

  protected abstract get _query(): string | null;
  protected abstract set _query(value: string | null);
}

export default BaseURL;



