/**
 * Polyfill for the URLSearchParams class.
 *
 * MDN documentation: https://developer.mozilla.org/en-US/docs/Web/API/URLSearchParams
 *
 * WhatWG Spec: https://url.spec.whatwg.org/
 */

import BaseURL from './BaseURL';

interface IKeyValuePair {
  key: string;
  value: string;
}

export default class URLSearchParams implements URLSearchParams {
  private _url: BaseURL;
  private _queryParameterList: IKeyValuePair[];

  /**
   * Parser following this spec: https://url.spec.whatwg.org/#concept-urlencoded-parser
   */
  private static _getParamterPairsFromString(str: string): IKeyValuePair[] {
    const strMatches: string[] | null = str.match(/\??(.*)/);
    if (strMatches) {
      str = strMatches[1];
    } else {
      // Unexpected format. Return nothing
      return [];
    }

    return (str.split('&') || []).filter((token: string) => token).map((token: string) => {
      const splitIndex: number = token.indexOf('=');
      let key: string = '';
      let value: string = '';

      if (splitIndex === -1) {
        // No "=" -> Empty value
        key = token;
      } else if (splitIndex === 0) {
        // "=" is the first character -> empty key
        value = token.substr(1);
      } else {
        key = token.substring(0, splitIndex);
        value = token.substr(splitIndex + 1);
      }

      // Replace "+" with space
      key = key.replace(/\+/g, ' ');
      value = value.replace(/\+/g, ' ');

      // Decode the key and value
      key = decodeURIComponent(key);
      value = decodeURIComponent(value);

      return { key, value };
    });
  }

  constructor(init?: string[][] | { [key: string]: string } | string) {
    this._initialize(init);
  }

  public append(key: string, value: string): void {
    this._queryParameterList.push({ key, value });
    this._update();
  }

  public delete(key: string): void {
    for (let i: number = 0; i < this._queryParameterList.length; i++) {
      if (this._queryParameterList[i].key === key) {
        this._queryParameterList.splice(i, 1);
        i--;
      }
    }

    this._update();
  }

  public get(key: string): string | null {
    for (let i: number = 0; i < this._queryParameterList.length; i++) {
      if (this._queryParameterList[i].key === key) {
        return this._queryParameterList[i].value;
      }
    }

    return null; // tslint:disable-line:no-null-keyword
  }

  public getAll(key: string): string[] {
    const result: string[] = [];
    for (let i: number = 0; i < this._queryParameterList.length; i++) {
      if (this._queryParameterList[i].key === key) {
        result.push(this._queryParameterList[i].value);
      }
    }

    return result;
  }

  public has(key: string): boolean {
    for (let i: number = 0; i < this._queryParameterList.length; i++) {
      if (this._queryParameterList[i].key === key) {
        return true;
      }
    }

    return false; // tslint:disable-line:no-null-keyword
  }

  public set(key: string, value: string): void {
    let found: boolean = false;
    for (let i: number = 0; i < this._queryParameterList.length; i++) {
      if (this._queryParameterList[i].key === key) {
        if (found) {
          // Already found a matching key - remove the rest
          this._queryParameterList.splice(i, 1);
          i--;
        } else {
          found = true;
          this._queryParameterList[i].value = value;
        }
      }
    }

    if (!found) {
      this.append(key, value);
      // No need to call _update() here because append() calls it.
    } else {
      this._update();
    }
  }

  public sort(): void {
    this._queryParameterList.sort((a: IKeyValuePair, b: IKeyValuePair): number => a.key.localeCompare(b.key));
    this._update();
  }

  public toString(): string {
    return this._queryParameterList.map(({ key, value }) =>
      `${encodeURIComponent(key)}=${encodeURIComponent(value)}`
    ).join('&');
  }

  private _update(): void {
    if (this._url) {
      this._url.search = this.toString();
    }
  }

  private _initialize(init?: string[][] | { [key: string]: string } | string): void {
    this._queryParameterList = [];

    if (Array.isArray(init)) {
      // Array
      for (const element of init) {
        if (!Array.isArray(element) || element.length !== 2) {
          // Must be an array of two-element arrays
          throw new TypeError('All elements in init sequence must have exactly two elements');
        } else {
          this._queryParameterList.push({
            key: element[0],
            value: element[1]
          });
        }
      }
    } else if (typeof init === 'object') {
      // Dictionary
      for (const key in init) {
        if (init.hasOwnProperty(key)) {
          this._queryParameterList.push({
            key,
            value: init[key]
          });
        }
      }
    } else if (typeof init === 'string') {
      // String
      this._queryParameterList = URLSearchParams._getParamterPairsFromString(init);
    } else {
      // do nothing
    }
  }
}



